html,
body {
  box-sizing: border-box; }

*,
*::before,
*::after {
  box-sizing: inherit; }

img {
  max-width: 100%;
  height: auto; }

.visually-hidden:not(:focus):not(:active),
input[type="checkbox"].visually-hidden,
input[type="radio"].visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden; }

.no-arrow::-webkit-outer-spin-button,
.no-arrow::-webkit-inner-spin-button {
  appearance: none; }

.no-arrow {
  appearance: textfield; }

.container {
  max-width: 1366px;
  margin: 0 auto;
  padding: 0 calc(50% - 683px);
  padding-left: 98px;
  padding-right: 98px; }

@media (max-width: 1023px) {
  .container {
    padding: 0 calc(50% - 384px);
    padding-left: 45px;
    padding-right: 45px; } }

@media (max-width: 767px) {
  .container {
    padding: 0 calc(50% - 160px);
    padding-left: 15px;
    padding-right: 15px; } }

.page-header__menu {
  display: flex;
  align-items: center;
  flex-wrap: wrap;
  padding-top: 30px; }

.page-header__search {
  margin-right: 32.7%; }

.page-header__logo {
  margin-right: 37%;
  width: 66px;
  height: 66px; }

@media (max-width: 1023px) {
  .page-header__wrapper {
    margin-left: 0px;
    margin-right: 0px; }
  .page-header__search {
    margin-right: 0; }
  .page-header__logo {
    margin-right: 42.4%; }
  .page-header__menu-toggle {
    margin-right: 41.45%; } }

@media (max-width: 767px) {
  .page-header__logo {
    margin-right: 34.83%;
    width: 52px;
    height: 52px; }
  .page-header__menu-toggle {
    margin-right: 32.41%; } }

.search__form {
  display: flex;
  align-items: flex-end; }

.search__form input {
  max-width: 140px;
  font-weight: 500;
  font-size: 14px;
  line-height: 21px;
  color: #9b9997;
  border: none;
  padding: 0; }
  .search__form input:hover, .search__form input:focus {
    color: #a87b62; }
  .search__form input:active {
    color: #a87b62; }

.search__form button {
  border: none;
  padding: 0;
  background-color: transparent;
  cursor: pointer;
  order: -1;
  margin-right: 15px; }
  .search__form button:hover svg,
  .search__form button:focus svg {
    fill: #a87b62; }
  .search__form button:active svg {
    fill: rgba(0, 0, 0, 0.7); }

.search__form svg {
  width: 14px;
  height: 14px; }

@media (max-width: 1023px) {
  .search {
    display: block;
    width: 100%;
    order: 4; }
  .search__form {
    max-width: 100%;
    width: 100%; }
  .search__form input {
    width: 100%; } }

.main-menu-toggle {
  display: none; }

@media (max-width: 1023px) {
  .main-menu-toggle {
    display: block;
    order: -1;
    border: none;
    background-color: transparent;
    padding: 0;
    cursor: pointer; }
    .main-menu-toggle:hover svg,
    .main-menu-toggle:focus svg {
      stroke: #a87b62; }
    .main-menu-toggle:active svg {
      stroke: rgba(0, 0, 0, 0.7); }
  .main-menu-toggle__icon svg {
    width: 25px;
    height: 16px;
    stroke: #1d1613; } }

.main-nav {
  display: flex; }

.main-nav__list {
  margin: 0;
  padding: 0;
  list-style: none;
  font-weight: 500;
  font-size: 14px;
  line-height: 21px; }

.main-nav__list--index {
  display: none; }

.main-nav__link {
  font-weight: 500;
  font-size: 14px;
  line-height: 21px;
  text-decoration: none;
  color: #1d1613; }
  .main-nav__link:hover, .main-nav__link:focus {
    color: #a87b62; }
  .main-nav__link:active {
    color: rgba(0, 0, 0, 0.7); }

@media (max-width: 1023px) {
  .main-nav {
    order: 6; }
  .main-nav__list--index {
    display: block;
    order: 4;
    width: 100%; }
  .main-nav__item {
    padding-top: 20px;
    padding-bottom: 20px; } }

.about-us__list--desktop {
  display: none; }

.about-us__link {
  font-weight: 500;
  font-size: 14px;
  line-height: 21px;
  text-decoration: none;
  color: #1d1613; }
  .about-us__link:hover, .about-us__link:focus {
    color: #a87b62; }
  .about-us__link:active {
    color: rgba(0, 0, 0, 0.7); }

@media (max-width: 1023px) {
  .about-us__list {
    display: block;
    width: 100%;
    order: 6; } }

.user-menu__list {
  margin-right: 3%; }

.user-menu__link {
  font-weight: 500;
  font-size: 14px;
  line-height: 21px;
  text-decoration: none;
  color: #1d1613; }
  .user-menu__link:hover, .user-menu__link:focus {
    color: #a87b62; }
  .user-menu__link:active {
    color: rgba(0, 0, 0, 0.7); }
  .user-menu__link svg {
    fill: transparent;
    display: none; }

.user-menu__link-label {
  margin-right: 3px; }

@media (max-width: 1023px) {
  .user-menu__list {
    order: 7;
    width: 100%;
    margin-right: 0; }
  .user-menu__link svg {
    display: block;
    width: 18px;
    height: 16px;
    stroke: #1d1613; }
    .user-menu__link svg:hover, .user-menu__link svg:focus {
      stroke: #a87b62; }
    .user-menu__link svg:active {
      stroke: rgba(0, 0, 0, 0.7); }
  .user-menu__link-label,
  .user-menu__link-value {
    display: none; } }

.body {
  min-width: 320px;
  font-family: "Poppins", "Arial", sans-serif;
  color: #1d1613;
  font-size: 16px;
  font-weight: normal;
  font-style: normal;
  line-height: normal;
  letter-spacing: normal; }

.modal--open {
  height: 100vh;
  overflow: hidden; }

.modal {
  position: fixed;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  opacity: 0;
  visibility: hidden;
  transform: scaleX(1.1) scaleY(1.1);
  transition: visibility 0s linear 0.25s, opacity 0.25s 0s, transform 0.25s;
  background-color: rgba(255, 255, 255, 0.9); }

.modal--show {
  opacity: 1;
  visibility: visible;
  transform: scaleX(1) scaleY(1);
  transition: visibility 0s linear 0s, opacity 0.25s 0s, transform 0.25s;
  z-index: 30; }
